html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* Fonts */
@font-face {
  font-family: "Montserrat";
  src: local("Montserrat"), local("Montserrat"), url("../../includes/fonts/Montserrat-Regular.woff2") format("woff2"), url("../../includes/fonts/Montserrat-Regular.woff") format("woff"), url("../../includes/fonts/Montserrat-Regular.ttf") format("truetype"), url("../../includes/fonts/Montserrat-Regular.eot"), url("../../includes/fonts/Montserrat-Regular.eot?#iefix") format("embedded-opentype");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat Bold";
  src: local("Montserrat Bold"), local("Montserrat"), url("../../includes/fonts/Montserrat-Bold.woff2") format("woff2"), url("../../includes/fonts/Montserrat-Bold.woff") format("woff"), url("../../includes/fonts/Montserrat-Bold.ttf") format("truetype"), url("../../includes/fonts/Montserrat-Bold.eot"), url("../../includes/fonts/Montserrat-Bold.eot?#iefix") format("embedded-opentype");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
/* Fonts */
@font-face {
  font-family: "Montserrat Light";
  src: local("Montserrat Light"), local("Montserrat"), url("../../includes/fonts/Montserrat-Light.woff2") format("woff2"), url("../../includes/fonts/Montserrat-Light.woff") format("woff"), url("../../includes/fonts/Montserrat-Light.ttf") format("truetype"), url("../../includes/fonts/Montserrat-Light.eot"), url("../../includes/fonts/Montserrat-Light.eot?#iefix") format("embedded-opentype");
  font-weight: 100;
  font-style: normal;
  font-display: swap;
}
/* Base */
body {
  background-color: #fff;
  color: #a0a0a0;
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  line-height: 1.36;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Montserrat Bold", Arial, Helvetica, sans-serif;
  color: #0c5e1f;
}

p,
a,
li,
span {
  font-size: 1rem;
}

.container {
  padding-right: 0.9375rem;
  padding-left: 0.9375rem;
  margin-right: auto;
  margin-left: auto;
  height: auto;
  width: auto;
}
@media (min-width: 768px) {
  .container {
    padding-right: 2.5rem;
    padding-left: 2.5rem;
  }
}
@media (min-width: 1200px) {
  .container {
    max-width: 1170px;
  }
}

.article-wrapper .article__picture, .main .sidebar__picture, .header-menu__picture {
  position: relative;
  display: block;
}

.article-wrapper .article__image, .main .sidebar__image, .header-menu__img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.header-menu__img {
  object-fit: contain;
}

.spinner {
  border: 8px solid rgba(0, 0, 0, 0.1);
  border-left-color: #46a84d;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  animation: spin 1s linear infinite;
  margin: 0 auto;
  display: none;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}
.header {
  width: 100%;
  background-color: #46a84d;
}
@media (min-width: 1024px) {
  .header {
    background-color: #46a84d;
    background: -moz-linear-gradient(90deg, #46a84d 0%, #0c5e1f 100%);
    background: -webkit-linear-gradient(90deg, #46a84d 0%, #0c5e1f 100%);
    background: -o-linear-gradient(90deg, #46a84d 0%, #0c5e1f 100%);
    background: -ms-linear-gradient(90deg, #46a84d 0%, #0c5e1f 100%);
    background: linear-gradient(90deg, #46a84d 0%, #0c5e1f 100%);
  }
}
.header-menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 3.75rem;
}
.header-menu__link {
  display: block;
  color: #fff;
  font-size: 1.375rem;
}
.header-menu__picture {
  width: 12.0625rem;
  height: 2.5rem;
}
@media (min-width: 768px) {
  .header-menu__picture {
    width: 9.0625rem;
  }
}
.header-content {
  padding-top: 0.9375rem;
  padding-bottom: 0.9375rem;
  background-color: #f5f5f5;
}
.header-content__title {
  font-size: 1.75rem;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .header-content__title {
    font-size: 2.25rem;
  }
}
.header-content__text {
  font-size: 0.875rem;
}
.header-breadcrumb {
  margin-bottom: 0.625rem;
}
.header-breadcrumb__item {
  font-size: 0.75rem;
  color: #0c5e1f;
}
.header-breadcrumb__item a {
  color: inherit;
  font-size: inherit;
}
.header-social {
  display: none;
}
@media (min-width: 1024px) {
  .header-social {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
.header-social__item {
  display: block;
  margin-right: 1.25rem;
  color: #fff;
  font-size: 1.25rem;
}

.footer {
  width: 100%;
  background-color: #f5f5f5;
}
.footer-disclaimer {
  padding: 2.5rem 0;
  text-align: center;
}
.footer-disclaimer__text {
  font-size: 0.75rem;
  margin-bottom: 1rem;
}
.footer-disclaimer__link {
  font-size: 0.75rem;
  color: #46a84d;
  text-decoration: none;
}
.footer-disclaimer__link:hover {
  text-decoration: none;
}

.main {
  padding: 1.25rem 0;
}
@media (min-width: 768px) {
  .main {
    padding: 2.5rem 0;
  }
}
@media (min-width: 1024px) {
  .main .container {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }
}
.main .sidebar {
  display: none;
  padding: 1.25rem;
  margin-left: 3%;
  width: 15.625rem;
  min-width: 15.625rem;
  border: 1px solid #0c5e1f;
}
@media (min-width: 1024px) {
  .main .sidebar {
    display: block;
  }
}
@media (min-width: 1024px) {
  .main .sidebar {
    width: 15.625rem;
    min-width: 15.625rem;
  }
}
@media (min-width: 1200px) {
  .main .sidebar {
    width: 18.75rem;
    min-width: 18.75rem;
  }
}
.main .sidebar__picture {
  margin: 0;
  width: 15.625rem;
  height: 13.125rem;
}
@media (min-width: 1200px) {
  .main .sidebar__picture {
    width: 18.75rem;
    height: 15.625rem;
  }
}
.main__btn {
  display: inline-block;
  padding: 1rem 1.5rem;
  margin-top: 1.875rem;
  font-size: 1rem;
  text-decoration: none;
  background-color: #46a84d;
  background: -moz-linear-gradient(90deg, #46a84d 0%, #0c5e1f 100%);
  background: -webkit-linear-gradient(90deg, #46a84d 0%, #0c5e1f 100%);
  background: -o-linear-gradient(90deg, #46a84d 0%, #0c5e1f 100%);
  background: -ms-linear-gradient(90deg, #46a84d 0%, #0c5e1f 100%);
  background: linear-gradient(90deg, #46a84d 0%, #0c5e1f 100%);
  border-radius: 10px;
  color: #fff;
  font-family: "Montserrat Bold", Arial, Helvetica, sans-serif;
}

.article-wrapper {
  display: inline-block;
}
@media (min-width: 1024px) {
  .article-wrapper {
    width: 70%;
  }
}
.article-wrapper .article {
  display: flex;
  margin-bottom: 1.5rem;
}
.article-wrapper .article__picture {
  margin-right: 0.625rem;
  width: 5rem;
  height: 3.5rem;
}
@media (min-width: 480px) {
  .article-wrapper .article__picture {
    width: 7.5rem;
    height: 5rem;
  }
}
@media (min-width: 640px) {
  .article-wrapper .article__picture {
    margin-right: 1.25rem;
    width: 11.25rem;
    height: 7.5rem;
  }
}
@media (min-width: 768px) {
  .article-wrapper .article__picture {
    margin-right: 1.875rem;
    width: 16.25rem;
    height: 11.25rem;
  }
}
.article-wrapper .article__image {
  border-radius: 10px;
}
.article-wrapper .article-content__title {
  font-size: 0.875rem;
  line-height: 1.2;
  margin-bottom: 10px;
  color: #0c5e1f;
}
.article-wrapper .article-content__title a {
  color: inherit;
  font-size: inherit;
  text-decoration: none;
}
@media (min-width: 768px) {
  .article-wrapper .article-content__title {
    font-size: 1.5rem;
  }
}
.article-wrapper .article-content__excerpt {
  display: none;
  margin-top: 0.625rem;
}
.article-wrapper .article-content__excerpt p {
  font-size: 0.875rem;
}
@media (min-width: 640px) {
  .article-wrapper .article-content__excerpt {
    display: inline-block;
  }
}
@media (min-width: 768px) {
  .article-wrapper .article-content__excerpt p {
    font-size: 1rem;
  }
}
.article-wrapper .article-meta {
  font-size: 0.75rem;
  text-rendering: auto;
  color: #a0a0a0;
}
.article-wrapper .article-meta__author, .article-wrapper .article-meta__date {
  display: none;
}
.article-wrapper .article-meta__author {
  text-transform: uppercase;
  font-size: 0.75rem;
}
@media (min-width: 480px) {
  .article-wrapper .article-meta__author {
    display: inline-block;
    margin-right: 0.625rem;
  }
}
.article-wrapper .article-meta__comment {
  display: inline-block;
}
.article-wrapper .article-meta__comment a {
  color: inherit;
  font-size: inherit;
  text-decoration: none;
}

.post-wrapper {
  display: inline-block;
  max-width: 100%;
}
@media (min-width: 1024px) {
  .post-wrapper {
    width: 70%;
  }
}
.post-wrapper .post-header {
  border-bottom: 2px solid #ececec;
  margin-bottom: 2rem;
}
.post-wrapper .post-header__title {
  margin-bottom: 1.25rem;
  font-size: 1.25rem;
}
@media (min-width: 1024px) {
  .post-wrapper .post-header__title {
    margin-bottom: 1.25rem;
    font-size: 2.625rem;
  }
}
.post-wrapper .post-header__subtitle {
  padding-bottom: 1rem;
  color: #a0a0a0;
  border-bottom: 2px solid #ececec;
  font-size: 1.5rem;
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
}
.post-wrapper .post-header img {
  max-width: 100%;
  margin-bottom: 1.25rem;
}
.post-wrapper .post-body h2 {
  margin-bottom: 1.25rem;
  font-size: 1.375rem;
}
.post-wrapper .post-body h3 {
  margin-bottom: 1.125rem;
  font-size: 1.125rem;
}
.post-wrapper .post-body h4 {
  margin-bottom: 1rem;
  font-size: 1rem;
}
.post-wrapper .post-body p,
.post-wrapper .post-body li,
.post-wrapper .post-body a {
  margin-bottom: 1rem;
  font-size: 0.875rem;
  word-break: break-all;
}
.post-wrapper .post-body iframe {
  max-width: 100%;
  height: auto;
}
@media (min-width: 1024px) {
  .post-wrapper .post-body h2 {
    margin-bottom: 1.375rem;
    font-size: 2rem;
  }
  .post-wrapper .post-body h3 {
    margin-bottom: 1.25rem;
    font-size: 1.75rem;
  }
  .post-wrapper .post-body h4 {
    margin-bottom: 1.125rem;
    font-size: 1.375rem;
  }
  .post-wrapper .post-body p,
.post-wrapper .post-body li,
.post-wrapper .post-body a {
    margin-bottom: 1.375rem;
    font-size: 1.125rem;
  }
}
.post-wrapper .post-body a {
  text-decoration: none;
  color: #46a84d;
}
.post-wrapper .post-body a:hover {
  text-decoration: underline;
}
.post-wrapper .post-body figure {
  display: block;
  text-align: center;
}
.post-wrapper .post-body img {
  display: block;
  width: auto;
  max-width: 100%;
  height: auto;
  object-fit: contain;
  margin: 1.25rem auto;
}
@media (min-width: 1024px) {
  .post-wrapper .post-body img {
    height: 100%;
    margin: 2.5rem auto;
  }
}